import React, { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { useNavigate } from "react-router-dom";
import { useAccount } from "wagmi";
import {
  Vote, Users, Wallet, FileText, Plus, ThumbsUp, ThumbsDown,
  MinusCircle, Clock, CheckCircle, XCircle, ArrowLeft, Sparkles
} from "lucide-react";
import MycelialTitle from "../MycelialTitle";
import GovernanceIcon from "../icons/GovernanceIcon";
import Footer from "../Footer";
import MycelialNetwork from "../MycelialNetwork";
import ScatteredBlockchainCubes from "../ScatteredBlockchainCubes";
import FlashlightCursor from "../FlashlightCursor";
import PsanctuaryWatermark from "../PsanctuaryWatermark";
import PageInfo from "../PageInfo";
import TrippyMushroom from "../TrippyMushroom";
import WalletConnectionBanner from "../WalletConnectionBanner";
import { useToast } from "../Toast";

/**
 * DAO Governance Page - The Mycelial Council
 * Full governance interface with proposals, voting, and treasury
 */
export default function DAO() {
  const { address, isConnected } = useAccount();
  const navigate = useNavigate();
  const { toast } = useToast();

  // Demo proposals for display
  const [proposals, setProposals] = useState([
    {
      id: 1,
      name: "Community Treasury Allocation",
      description: "Allocate 10 ETH from treasury for community development initiatives",
      amount: "10",
      recipient: "0x15d34AAf54267DB7D7c367839AAf71A00a2C6A65",
      forVotes: 450000,
      againstVotes: 50000,
      abstainVotes: 100000,
      status: "active",
      deadline: Date.now() + 86400000 * 3, // 3 days from now
      creator: "0xf39Fd6e51aad88F6F4ce6aB8827279cffFb92266"
    },
    {
      id: 2,
      name: "New Strain Research Grant",
      description: "Fund research into rare mycelial strains for cross-breeding program",
      amount: "5",
      recipient: "0x3C44CdDdB6a900fa2b585dd299e03d12FA4293BC",
      forVotes: 300000,
      againstVotes: 200000,
      abstainVotes: 50000,
      status: "active",
      deadline: Date.now() + 86400000 * 5, // 5 days from now
      creator: "0x70997970C51812dc3A010C7d01b50e0d17dc79C8"
    },
    {
      id: 3,
      name: "Ceremony Venue Acquisition",
      description: "Purchase sacred land for community ceremonies and gatherings",
      amount: "50",
      recipient: "0x90F79bf6EB2c4f870365E785982E1f101E93b906",
      forVotes: 600000,
      againstVotes: 100000,
      abstainVotes: 50000,
      status: "finalized",
      deadline: Date.now() - 86400000, // Past
      creator: "0xf39Fd6e51aad88F6F4ce6aB8827279cffFb92266"
    }
  ]);

  const [treasuryBalance] = useState("125.5");
  const [quorum] = useState(500000);
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [userVotes, setUserVotes] = useState({});

  // Check if user has voted on a proposal
  const hasVoted = (proposalId) => userVotes[proposalId] !== undefined;

  // Handle voting
  const handleVote = (proposalId, voteType) => {
    if (!isConnected) {
      toast.warning("Please connect your wallet to vote", { title: "Wallet Required" });
      return;
    }

    // Demo mode - just update local state
    setUserVotes(prev => ({ ...prev, [proposalId]: voteType }));

    const voteLabels = { 1: "For", "-1": "Against", 2: "Abstain" };
    toast.success(`Vote cast: ${voteLabels[voteType]}`, {
      title: "Vote Recorded üó≥Ô∏è",
      duration: 3000
    });
  };

  // Format address for display
  const formatAddress = (addr) => `${addr.slice(0, 6)}...${addr.slice(-4)}`;

  // Get status badge
  const getStatusBadge = (proposal) => {
    if (proposal.status === "finalized") {
      return (
        <span style={{
          background: "linear-gradient(135deg, rgba(34, 197, 94, 0.3), rgba(22, 163, 74, 0.3))",
          border: "1px solid rgba(34, 197, 94, 0.5)",
          borderRadius: "8px",
          padding: "4px 12px",
          color: "#22c55e",
          fontSize: "0.85rem",
          fontWeight: "600"
        }}>
          <CheckCircle size={14} style={{ marginRight: 4 }} /> Approved
        </span>
      );
    }
    if (proposal.status === "cancelled") {
      return (
        <span style={{
          background: "linear-gradient(135deg, rgba(239, 68, 68, 0.3), rgba(220, 38, 38, 0.3))",
          border: "1px solid rgba(239, 68, 68, 0.5)",
          borderRadius: "8px",
          padding: "4px 12px",
          color: "#ef4444",
          fontSize: "0.85rem",
          fontWeight: "600"
        }}>
          <XCircle size={14} style={{ marginRight: 4 }} /> Cancelled
        </span>
      );
    }
    const totalVotes = proposal.forVotes + proposal.againstVotes;
    if (totalVotes >= quorum) {
      return (
        <span style={{
          background: "linear-gradient(135deg, rgba(245, 158, 11, 0.3), rgba(217, 119, 6, 0.3))",
          border: "1px solid rgba(245, 158, 11, 0.5)",
          borderRadius: "8px",
          padding: "4px 12px",
          color: "#f59e0b",
          fontSize: "0.85rem",
          fontWeight: "600"
        }}>
          üéØ Ready to Finalize
        </span>
      );
    }
    return (
      <span style={{
        background: "linear-gradient(135deg, rgba(59, 130, 246, 0.3), rgba(37, 99, 235, 0.3))",
        border: "1px solid rgba(59, 130, 246, 0.5)",
        borderRadius: "8px",
        padding: "4px 12px",
        color: "#3b82f6",
        fontSize: "0.85rem",
        fontWeight: "600"
      }}>
        <Clock size={14} style={{ marginRight: 4 }} /> In Progress
      </span>
    );
  };

  // Calculate vote percentages
  const getVotePercentages = (proposal) => {
    const total = proposal.forVotes + proposal.againstVotes + proposal.abstainVotes;
    if (total === 0) return { for: 0, against: 0, abstain: 0 };
    return {
      for: Math.round((proposal.forVotes / total) * 100),
      against: Math.round((proposal.againstVotes / total) * 100),
      abstain: Math.round((proposal.abstainVotes / total) * 100)
    };
  };

  // Format time remaining
  const formatTimeRemaining = (deadline) => {
    const now = Date.now();
    const diff = deadline - now;
    if (diff <= 0) return "Ended";
    const days = Math.floor(diff / 86400000);
    const hours = Math.floor((diff % 86400000) / 3600000);
    if (days > 0) return `${days}d ${hours}h remaining`;
    return `${hours}h remaining`;
  };

  return (
    <div
      className="page-container"
      style={{
        position: 'relative',
        backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.85)), url(${process.env.PUBLIC_URL}/images/fruits.png)`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundAttachment: 'fixed',
        minHeight: '100vh'
      }}
    >
      <FlashlightCursor />
      <PsanctuaryWatermark opacity={0.12} size={800} />
      <ScatteredBlockchainCubes />
      <MycelialNetwork sourceId="dao-title" />
      <PageInfo pageTitle="Mycelial Council" description="The DAO governance chamber. Create proposals, vote on community decisions, and shape the future together." />
      {!isConnected && <WalletConnectionBanner message="Connect your wallet to participate in governance" />}

      <motion.div
        initial={{ opacity: 0, y: -30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 1 }}
        className="page-content"
        style={{ position: 'relative', zIndex: 2, padding: '20px' }}
      >
        {/* Back to Governance button */}
        <div style={{ maxWidth: '1200px', margin: '0 auto 20px', paddingTop: '80px' }}>
          <motion.button
            onClick={() => navigate('/governance')}
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
            style={{
              background: 'rgba(59, 130, 246, 0.2)',
              border: '1px solid rgba(59, 130, 246, 0.4)',
              borderRadius: '8px',
              padding: '8px 16px',
              color: '#3b82f6',
              cursor: 'pointer',
              display: 'inline-flex',
              alignItems: 'center',
              gap: '8px',
              fontSize: '0.9rem'
            }}
          >
            <ArrowLeft size={16} /> Back to Governance
          </motion.button>
        </div>

        {/* Title */}
        <div id="dao-title" style={{ textAlign: 'center', marginBottom: '32px' }}>
          <MycelialTitle icon={GovernanceIcon} title="Mycelial Council" size={64} />
          <p style={{ color: '#a78bfa', fontSize: '1.1rem', marginTop: '12px' }}>
            Where the network speaks with one voice üçÑ
          </p>
        </div>

        {/* Stats Cards */}
        <div style={{
          maxWidth: '1200px',
          margin: '0 auto 40px',
          display: 'grid',
          gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))',
          gap: '20px'
        }}>
          {/* Treasury Card */}
          <motion.div
            whileHover={{ scale: 1.02, y: -4 }}
            style={{
              background: 'linear-gradient(135deg, rgba(34, 197, 94, 0.15), rgba(22, 163, 74, 0.1))',
              border: '2px solid rgba(34, 197, 94, 0.4)',
              borderRadius: '16px',
              padding: '24px',
              textAlign: 'center'
            }}
          >
            <Wallet size={32} style={{ color: '#22c55e', marginBottom: '12px' }} />
            <h3 style={{ color: '#22c55e', fontSize: '2rem', margin: '0 0 8px' }}>{treasuryBalance} ETH</h3>
            <p style={{ color: '#9ca3af', margin: 0 }}>Treasury Balance</p>
          </motion.div>

          {/* Active Proposals Card */}
          <motion.div
            whileHover={{ scale: 1.02, y: -4 }}
            style={{
              background: 'linear-gradient(135deg, rgba(59, 130, 246, 0.15), rgba(37, 99, 235, 0.1))',
              border: '2px solid rgba(59, 130, 246, 0.4)',
              borderRadius: '16px',
              padding: '24px',
              textAlign: 'center'
            }}
          >
            <FileText size={32} style={{ color: '#3b82f6', marginBottom: '12px' }} />
            <h3 style={{ color: '#3b82f6', fontSize: '2rem', margin: '0 0 8px' }}>
              {proposals.filter(p => p.status === 'active').length}
            </h3>
            <p style={{ color: '#9ca3af', margin: 0 }}>Active Proposals</p>
          </motion.div>

          {/* Quorum Card */}
          <motion.div
            whileHover={{ scale: 1.02, y: -4 }}
            style={{
              background: 'linear-gradient(135deg, rgba(168, 85, 247, 0.15), rgba(139, 92, 246, 0.1))',
              border: '2px solid rgba(168, 85, 247, 0.4)',
              borderRadius: '16px',
              padding: '24px',
              textAlign: 'center'
            }}
          >
            <Users size={32} style={{ color: '#a855f7', marginBottom: '12px' }} />
            <h3 style={{ color: '#a855f7', fontSize: '2rem', margin: '0 0 8px' }}>
              {(quorum / 1000).toFixed(0)}K
            </h3>
            <p style={{ color: '#9ca3af', margin: 0 }}>Quorum Required</p>
          </motion.div>

          {/* Your Voting Power Card */}
          <motion.div
            whileHover={{ scale: 1.02, y: -4 }}
            style={{
              background: 'linear-gradient(135deg, rgba(245, 158, 11, 0.15), rgba(217, 119, 6, 0.1))',
              border: '2px solid rgba(245, 158, 11, 0.4)',
              borderRadius: '16px',
              padding: '24px',
              textAlign: 'center'
            }}
          >
            <Vote size={32} style={{ color: '#f59e0b', marginBottom: '12px' }} />
            <h3 style={{ color: '#f59e0b', fontSize: '2rem', margin: '0 0 8px' }}>
              {isConnected ? '200K' : '‚Äî'}
            </h3>
            <p style={{ color: '#9ca3af', margin: 0 }}>Your Voting Power</p>
          </motion.div>
        </div>

        {/* Create Proposal Button */}
        <div style={{ maxWidth: '1200px', margin: '0 auto 32px', textAlign: 'center' }}>
          <motion.button
            onClick={() => {
              if (!isConnected) {
                toast.warning("Connect your wallet to create proposals", { title: "Wallet Required" });
                return;
              }
              setShowCreateForm(!showCreateForm);
            }}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            style={{
              background: 'linear-gradient(135deg, rgba(34, 197, 94, 0.3), rgba(22, 163, 74, 0.3))',
              border: '2px solid rgba(34, 197, 94, 0.5)',
              borderRadius: '12px',
              padding: '14px 28px',
              color: '#22c55e',
              fontSize: '1.1rem',
              fontWeight: '600',
              cursor: 'pointer',
              display: 'inline-flex',
              alignItems: 'center',
              gap: '10px'
            }}
          >
            <Plus size={20} />
            Create New Proposal
          </motion.button>
        </div>

        {/* Proposals List */}
        <div style={{ maxWidth: '1200px', margin: '0 auto' }}>
          <h2 style={{
            color: '#e5e7eb',
            fontSize: '1.5rem',
            marginBottom: '24px',
            display: 'flex',
            alignItems: 'center',
            gap: '12px'
          }}>
            <FileText size={24} style={{ color: '#3b82f6' }} />
            Active Proposals
          </h2>

          {proposals.map((proposal) => {
            const percentages = getVotePercentages(proposal);
            const isActive = proposal.status === 'active';

            return (
              <motion.div
                key={proposal.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                whileHover={{ scale: 1.01 }}
                style={{
                  background: 'linear-gradient(135deg, rgba(17, 24, 39, 0.9), rgba(31, 41, 55, 0.9))',
                  border: '2px solid rgba(75, 85, 99, 0.4)',
                  borderRadius: '16px',
                  padding: '24px',
                  marginBottom: '20px'
                }}
              >
                {/* Proposal Header */}
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', marginBottom: '16px', flexWrap: 'wrap', gap: '12px' }}>
                  <div>
                    <h3 style={{ color: '#e5e7eb', fontSize: '1.25rem', margin: '0 0 8px' }}>
                      #{proposal.id} - {proposal.name}
                    </h3>
                    <p style={{ color: '#9ca3af', margin: 0, fontSize: '0.9rem' }}>
                      {proposal.description}
                    </p>
                  </div>
                  {getStatusBadge(proposal)}
                </div>

                {/* Proposal Details */}
                <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(150px, 1fr))', gap: '16px', marginBottom: '20px' }}>
                  <div>
                    <span style={{ color: '#6b7280', fontSize: '0.85rem' }}>Amount</span>
                    <p style={{ color: '#22c55e', fontWeight: '600', margin: '4px 0 0' }}>{proposal.amount} ETH</p>
                  </div>
                  <div>
                    <span style={{ color: '#6b7280', fontSize: '0.85rem' }}>Recipient</span>
                    <p style={{ color: '#a78bfa', fontWeight: '600', margin: '4px 0 0' }}>{formatAddress(proposal.recipient)}</p>
                  </div>
                  <div>
                    <span style={{ color: '#6b7280', fontSize: '0.85rem' }}>Creator</span>
                    <p style={{ color: '#60a5fa', fontWeight: '600', margin: '4px 0 0' }}>{formatAddress(proposal.creator)}</p>
                  </div>
                  <div>
                    <span style={{ color: '#6b7280', fontSize: '0.85rem' }}>Deadline</span>
                    <p style={{ color: isActive ? '#f59e0b' : '#6b7280', fontWeight: '600', margin: '4px 0 0' }}>
                      {formatTimeRemaining(proposal.deadline)}
                    </p>
                  </div>
                </div>

                {/* Vote Progress Bar */}
                <div style={{ marginBottom: '20px' }}>
                  <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '8px', fontSize: '0.85rem' }}>
                    <span style={{ color: '#22c55e' }}>For: {percentages.for}%</span>
                    <span style={{ color: '#6b7280' }}>Abstain: {percentages.abstain}%</span>
                    <span style={{ color: '#ef4444' }}>Against: {percentages.against}%</span>
                  </div>
                  <div style={{ height: '8px', borderRadius: '4px', background: 'rgba(75, 85, 99, 0.4)', overflow: 'hidden', display: 'flex' }}>
                    <div style={{ width: `${percentages.for}%`, background: '#22c55e', transition: 'width 0.3s' }} />
                    <div style={{ width: `${percentages.abstain}%`, background: '#6b7280', transition: 'width 0.3s' }} />
                    <div style={{ width: `${percentages.against}%`, background: '#ef4444', transition: 'width 0.3s' }} />
                  </div>
                </div>

                {/* Voting Buttons */}
                {isActive && (
                  <div style={{ display: 'flex', gap: '12px', flexWrap: 'wrap' }}>
                    {hasVoted(proposal.id) ? (
                      <div style={{
                        background: 'rgba(34, 197, 94, 0.2)',
                        border: '1px solid rgba(34, 197, 94, 0.4)',
                        borderRadius: '8px',
                        padding: '12px 20px',
                        color: '#22c55e',
                        display: 'flex',
                        alignItems: 'center',
                        gap: '8px'
                      }}>
                        <CheckCircle size={18} />
                        You voted: {userVotes[proposal.id] === 1 ? 'For' : userVotes[proposal.id] === -1 ? 'Against' : 'Abstain'}
                      </div>
                    ) : (
                      <>
                        <motion.button
                          onClick={() => handleVote(proposal.id, 1)}
                          whileHover={{ scale: 1.05 }}
                          whileTap={{ scale: 0.95 }}
                          style={{
                            background: 'rgba(34, 197, 94, 0.2)',
                            border: '1px solid rgba(34, 197, 94, 0.5)',
                            borderRadius: '8px',
                            padding: '10px 20px',
                            color: '#22c55e',
                            cursor: 'pointer',
                            display: 'flex',
                            alignItems: 'center',
                            gap: '6px'
                          }}
                        >
                          <ThumbsUp size={16} /> Vote For
                        </motion.button>
                        <motion.button
                          onClick={() => handleVote(proposal.id, 2)}
                          whileHover={{ scale: 1.05 }}
                          whileTap={{ scale: 0.95 }}
                          style={{
                            background: 'rgba(107, 114, 128, 0.2)',
                            border: '1px solid rgba(107, 114, 128, 0.5)',
                            borderRadius: '8px',
                            padding: '10px 20px',
                            color: '#9ca3af',
                            cursor: 'pointer',
                            display: 'flex',
                            alignItems: 'center',
                            gap: '6px'
                          }}
                        >
                          <MinusCircle size={16} /> Abstain
                        </motion.button>
                        <motion.button
                          onClick={() => handleVote(proposal.id, -1)}
                          whileHover={{ scale: 1.05 }}
                          whileTap={{ scale: 0.95 }}
                          style={{
                            background: 'rgba(239, 68, 68, 0.2)',
                            border: '1px solid rgba(239, 68, 68, 0.5)',
                            borderRadius: '8px',
                            padding: '10px 20px',
                            color: '#ef4444',
                            cursor: 'pointer',
                            display: 'flex',
                            alignItems: 'center',
                            gap: '6px'
                          }}
                        >
                          <ThumbsDown size={16} /> Vote Against
                        </motion.button>
                      </>
                    )}
                  </div>
                )}
              </motion.div>
            );
          })}
        </div>

        {/* Trippy Mushrooms - Increased for DAO governance ambiance */}
        {[
          // Original positions
          { top: '10%', left: '5%', delay: 0, size: 70 },
          { top: '20%', right: '8%', delay: 1.2, size: 60 },
          { bottom: '30%', left: '8%', delay: 2.5, size: 80 },
          { bottom: '15%', right: '5%', delay: 1.8, size: 65 },
          { top: '50%', left: '3%', delay: 0.8, size: 55 },
          { top: '60%', right: '4%', delay: 2.0, size: 75 },
          // Additional mushrooms for mycelial council atmosphere
          { top: '8%', right: '18%', delay: 0.5, size: 68 },
          { top: '15%', left: '12%', delay: 2.2, size: 72 },
          { top: '35%', left: '5%', delay: 1.5, size: 62 },
          { top: '45%', right: '6%', delay: 0.3, size: 78 },
          { bottom: '40%', right: '10%', delay: 2.8, size: 58 },
          { bottom: '25%', left: '12%', delay: 1.0, size: 85 },
          { top: '70%', left: '6%', delay: 2.3, size: 52 },
          { bottom: '8%', right: '12%', delay: 0.7, size: 76 }
        ].map((pos, i) => (
          <motion.div
            key={i}
            style={{ position: 'absolute', ...pos, pointerEvents: 'none', zIndex: 1 }}
            initial={{ opacity: 0, scale: 0 }}
            animate={{ opacity: [0, 0.8, 0.8], scale: [0, 1.1, 1], y: [0, -8, 0] }}
            transition={{ duration: 3, delay: pos.delay, repeat: Infinity, repeatDelay: 2 }}
          >
            <TrippyMushroom size={pos.size} delay={pos.delay} />
          </motion.div>
        ))}

        <Footer />
      </motion.div>
    </div>
  );
}

